import React from 'react';
import axios from "axios";
import { render } from '@testing-library/react';
import Movie from "../components/Movie";
import "./Home.css";

class Home extends React.Component {
  state = {
    isLoading: true,
    movies: []
  };
  getMovies = async() => { //async = 이 함수가 실행되기 까지 좀 걸린다는 뜻
    const {
      data : {
        data: {movies}
      }
    } = await axios.get("https://yts-proxy.nomadcoders1.now.sh/list_movies.json"); //await은 무엇을 기다리는지 지정해 주는 것.
    this.setState({movies, isLoading: false});
    console.log(movies);
  };
   componentDidMount(){
    this.getMovies();
  };
  render() {
    const { isLoading, movies } = this.state;
    return (
      <section className="container">
        {isLoading
        ? (<div className="loader">
          <span className="loader__text">Loding...</span>
          </div>
          ) : ( 
            <div className="movies">
                {movies.map(movie => (
                  <Movie 
                    key={movie.id}
                    id={movie.id} 
                    year={movie.year} 
                    title={movie.title} 
                    summary={movie.summary} 
                    poster={movie.medium_cover_image}
                    genres={movie.genres}
              />
            ))}
            </div>
          )}
      </section>
    )
  }
  
}

export default Home;